@media (max-width: 768px) {
  /* ---------------- max 768 --------------------------*/
  .container {
    max-width: 540px;
  }
  .header {
    justify-content: space-around;
  }
  .header__phone {
    flex: unset;
    align-items: center;
  }
  .header__h2 {
    line-height: 42px;
  }
  .header .menu__header {
    position: fixed;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    top: 0;
    left: -100%;
    width: 50%;
    /** половина от родительского элемента **/
    height: 100vh;
    /** 100% от видимого экрана **/
    padding: 20px 15px;
    background-color: rgba(0, 0, 0, 0.8);
    transition: 0.6s all;
    margin-left: 0;
  }
  .header .menu__header_active {
    left: 0;
  }
  .header .menu__header_item {
    padding-right: 0;
    border: none;
    margin: 10px;
    border-bottom: 2px solid  #ec644b;
    border-left: 2px solid #e03819;
    padding: 5px;
  }
  .header .menu__header_link {
    font-size: 20px;
  }
  .first {
    padding-bottom: 30px;
  }
  .first__descr {
    line-height: 20px;
  }
  .first__flex_item {
    width: 100px;
    margin-top: 10px;
  }
  .advantages__wrapper {
    align-items: center;
    flex-direction: column;
  }
  .hamburger {
    display: block;
    position: fixed;
    top: 25px;
    right: 25px;
    transform: translateY(-50%);
    width: 26px;
    height: 12px;
    z-index: 10;
  }
  .hamburger span {
    display: block;
    height: 4px;
    width: 100%;
    background-color: #00c2f0;
    margin-bottom: 2px;
    transition: 0.4s all;
    border-radius: 25%;
  }
  .hamburger_active span {
    margin-bottom: -4px;
  }
  .hamburger_active span:nth-child(1) {
    transform: translateY(3px) rotate(-45deg);
  }
  .hamburger_active span:nth-child(2) {
    display: none;
  }
  .hamburger_active span:nth-child(3) {
    transform: translateY(3px) rotate(45deg);
  }
  .footer {
    padding-top: 13px;
    padding-bottom: 13px;
  }
  .footer__header {
    flex-wrap: wrap;
    padding: 0 10px 13px 10px;
  }
  .footer__title {
    margin-left: 0;
  }
  .footer__phone {
    align-items: flex-start;
  }
  .footer__descr {
    margin-top: 12px;
  }
  .pageup {
    bottom: 15px;
    right: 10px;
  }
  .pageup svg {
    width: 30px;
    height: 30px;
  }
  .scheme__wrapper_1 {
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .scheme__wrapper_2 {
    margin-top: 10px;
  }
  .scheme__item {
    padding-bottom: 15px;
  }
}

